import React, { useEffect, useState } from "react";
export default function Question(props) {


  return (
    <div
    className="wrap-box-item hover-product"
    style={{
      border: "1px solid rgb(202, 198, 198)",
      borderRadius: "0.375rem",
    }}
  >
    <div className style={{ padding: "10px" }}>
      {/* <div style={{ width: "100%" }}>
        {" "}
        <img
          src="https://data1.ikitech.vn/public/api/SahaImages/PDcgrWYznD1657165748.webp"
          style={{ margin: "auto", width: "160px", height: "160px" }}
        />
      </div> */}
      <div className="question">Trúc có xinh không</div>
      <div className="answer">
        <div className="item-result">A. không</div>
        <div className="item-result">B. quá xinh</div>
        <div className="item-result">C. bình thường</div>
        <div className="item-result">D. tạm đc</div>
        <div className="item-result-correct">Kết quả đúng B</div>
      </div>
    </div>
    <div className="action-box">
      <button
        className="btn-not-background edit"
        onClick={
          props.handleUpdate
         }
         data-toggle="modal"
         data-target="#modalUID"
      >
        <i className="fa fa-pencil" />
      </button>
      <button
        className="btn-not-background remove"
        onClick={
          props.handleDelete
         }
         data-toggle="modal"
         data-target="#modalUID"
      >
        <i className="fa fa-trash" />
      </button>
    </div>
  </div>
  );
}
