import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "../style.css";
import Item from "./child/Item";
import Create from "./child/Create";
import Edit from "./child/Edit";
import Delete from "./child/Delete";
import ModalUID from "../../../components/ModalUID";

export default function Course() {

  const [state, setState] = useState({
    isOpenLesson: false,
    modal: {
      componentAction: null,
      title: "",
      method: null
    }
  });

  const { modal, isOpenLesson } = state;
  function handleDelete() {
    setState({ ...state, modal: { componentAction: <Delete></Delete>, title: "Xóa câu hỏi", method: "REMOVE" } })
  }

  function handleUpdate() {
    console.log("ssss")
    setState({ ...state, modal: { componentAction: <Edit></Edit>, title: "Sửa câu hỏi", method: "EDIT" } })

  }

  return (
    <div className="container-fluid">
 
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">
          Câu hỏi trắc nghiệm - Bài test 01
        </h4>
        <div style={{ display: "flex" }}>
          <button
            style={{ marginRight: "10px" }}
            class={`btn btn-danger btn-icon-split btn-sm  `}
          >
            <span class="icon text-white-50">
              <i class="fas fa-file-export"></i>
            </span>
            <span style={{ color: "white" }} class="text">
              Export Excel
            </span>
          </button>
          <button
            style={{ marginRight: "10px" }}
            class={`btn btn-primary btn-icon-split btn-sm  `}
          >
            <span class="icon text-white-50">
              <i class="fas fa-file-import"></i>
            </span>
            <span style={{ color: "white" }} class="text">
              Import Excel
            </span>
          </button>
        <button
          onClick={() =>
            setState({ ...state, modal: { componentAction: <Create></Create>, title: "Thêm câu hỏi", method: "CREATE" } })
          }
          data-toggle="modal"
          data-target="#modalUID"
          className="btn btn-info btn-icon-split btn-sm"
        >
          <span className="icon text-white-50">
            <i className="fas fa-plus" />
          </span>
          <span className="text">Thêm câu hỏi</span>
        </button>
      </div>
      </div>

      <div className="card shadow mb-4">
        <div className="card-body">
          <Item
            handleDelete={handleDelete}
            handleUpdate={handleUpdate}></Item>
          <Item
            handleDelete={handleDelete}
            handleUpdate={handleUpdate}></Item>  <Item
              handleDelete={handleDelete}
              handleUpdate={handleUpdate}></Item>  <Item
                handleDelete={handleDelete}
                handleUpdate={handleUpdate}></Item>

        </div>
      </div>
      <ModalUID method={modal.method} title={modal.title}> {modal.componentAction} </ModalUID>

    </div>
  );
}
