import React, { useEffect, useState } from "react";

export default function ModalCreate() {
  const [state, setState] = useState({
    name: "",
  });

  const { name } = state;


  function onChange(e) {
    var { name, value } = e.target;
    setState({ ...state, [name]: value });
  }

  return (
    <form
      role="form"
      action="#"
      method="post"
      id="createForm"
    >
      <div class="form-group">
        <label for="product_name">Câu hỏi</label>

        <textarea
          placeholder="Nhập câu hỏi"
          autocomplete="off"
          name="question" class="form-control" rows="2" ></textarea>


      </div>
      <div class="form-group">
        <label for="product_name">Câu trả lời A</label>
        <input
          type="text"
          class="form-control"
          id="answer_a"
          placeholder="Nhập câu trả lời"
          autocomplete="off"
          name="answer_a"
        />
      </div>
      <div class="form-group">
        <label for="product_name">Câu trả lời B</label>
        <input
          type="text"
          class="form-control"
          id="answer_b"
          placeholder="Nhập câu trả lời"
          autocomplete="off"
          name="answer_b"
        />
      </div>
      <div class="form-group">
        <label for="product_name">Câu trả lời C</label>
        <input
          type="text"
          class="form-control"
          id="answer_c"
          placeholder="Nhập câu trả lời"
          autocomplete="off"
          name="answer_c"
        />
      </div>
      <div class="form-group">
        <label for="product_name">Câu trả lời D</label>
        <input
          type="text"
          class="form-control"
          id="answer_d"
          placeholder="Nhập câu trả lời"
          autocomplete="off"
          name="answer_d"
        />
      </div>
      <div class="form-group">
        <label for="product_name">Câu trả lời đúng</label>

        <select name="right_answer" id="input" class="form-control"
        >
          <option value="A">A</option>
          <option value="B">B</option>

          <option value="C">C</option>
          <option value="D">D</option>

        </select>

      </div>







    </form>
  );
}
